import React from 'react';
import { useTable } from 'react-table';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faTrash } from '@fortawesome/free-solid-svg-icons';

const PlayerTable = ({ players, setPlayers, showDetails, numPlayers, setNumPlayers }) => {
  const handleInputChange = (rowId, columnId, value) => {
    setPlayers((prevPlayers) =>
      prevPlayers.map((player) =>
        player.id === rowId ? { ...player, [columnId]: value } : player
      )
    );
  };

  const handleDeletePlayer = (playerId) => {
    setPlayers((prevPlayers) => {
      const updatedPlayers = prevPlayers.filter((player) => player.id !== playerId);
      setNumPlayers(updatedPlayers.length);  // Update numPlayers
      return updatedPlayers;
    });
  };

  const renderEditable = (cell) => {
    if (cell.column.id === 'rating') {
      return (
        <select
          value={cell.value || 3}
          onChange={(e) => handleInputChange(cell.row.original.id, cell.column.id, e.target.value)}
          style={{ width: '120px' }}
        >
          <option value="1">1: Beginner (new to futsal)</option>
          <option value="2">2</option>
          <option value="3">3: Neutral</option>
          <option value="4">4</option>
          <option value="5">5: Strong (top experienced player)</option>
        </select>
      );
    }
    return (
      <input
        value={cell.value || ''}
        onChange={(e) => handleInputChange(cell.row.original.id, cell.column.id, e.target.value)}
        style={{ width: '120px' }}
      />
    );
  };

  const columns = React.useMemo(() => [
    {
      id: 'delete',
      width: 50,
      Cell: ({ row }) => (
        <span
          onClick={() => handleDeletePlayer(row.original.id)}
          style={{
            cursor: 'pointer',
            color: 'red',
            fontWeight: 'bold',
            fontSize: '1.2em',
          }}
          title="Remove player"
        >
          ×
        </span>
      ),
    },
    {
      Header: 'Name',
      accessor: 'name',
      Cell: renderEditable,
    },
    {
      Header: 'Rating',
      accessor: 'rating',
      Cell: renderEditable,
    },
  ], [showDetails]);  

  const data = React.useMemo(() => players, [players]);

  const {
    getTableProps,
    getTableBodyProps,
    headerGroups,
    rows,
    prepareRow,
  } = useTable({
    columns,
    data,
  });

  return (
    <table {...getTableProps()} className="player-table table-striped table-bordered">
      <thead>
        {headerGroups.map((headerGroup) => (
          <tr {...headerGroup.getHeaderGroupProps()}>
            {headerGroup.headers.map((column) => (
              <th {...column.getHeaderProps()}>{column.render('Header')}</th>
            ))}
          </tr>
        ))}
      </thead>
      <tbody {...getTableBodyProps()}>
        {rows.map((row) => {
          prepareRow(row);
          return (
            <tr {...row.getRowProps()}>
              {row.cells.map((cell) => (
                <td {...cell.getCellProps()}>{cell.render('Cell')}</td>
              ))}
            </tr>
          );
        })}
      </tbody>
    </table>
  );
};

export default PlayerTable;
